.card {
  border: 2px solid #000;
  border-radius: 10px;
  background: #fff;
  overflow: hidden;
  flex-basis: 30%;
}
.card-title {
  margin: 10px 0;
}
.card-detail {
  padding: 0 1em;
}

button, .btn {
  text-decoration: none;
  border-radius: 10px;
  padding: 0.65em 1.5em;
  display: inline-block;
  text-align: center;
  max-width: 150px;
  border: none;
  transition: all 0.2s linear;
  font-size: 1rem;
}
button:hover, .btn:hover {
  cursor: pointer;
}
button[type=submit], [type=submit].btn {
  background: #ed1c24;
  color: #fff;
}
button[type=submit]:hover, [type=submit].btn:hover {
  transform: scale(1.15);
}
button[type=button], [type=button].btn {
  background: #64b7da;
  color: #fff;
}
button[type=button]:not([disabled]):hover, [type=button].btn:not([disabled]):hover {
  transform: scale(1.15);
}
button[disabled], [disabled].btn {
  color: #838383;
  background: #cfcfcf;
  cursor: not-allowed;
}

.cta-1 {
  background: #ed1c24;
  color: #fff;
}
.cta-1:hover {
  transform: scale(1.15);
}
.cta-2 {
  background: #64b7da;
  color: #fff;
}
.cta-2:hover {
  transform: scale(1.15);
}
.cta-3 {
  background: transparent;
  color: #20303B;
  box-shadow: inset 0 0 0 2px #20303B;
}
.cta-3:hover {
  transform: scale(1.15);
}

html,
body {
  font-size: 16px;
  margin: 0;
  padding: 0;
}

p {
  margin-top: 0;
  margin-bottom: 1em;
}

h1, h2, h3,
h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 1rem;
}

ul, ol, dl {
  margin: 0;
}

details {
  text-align: left;
  overflow: hidden;
}
details:hover {
  cursor: pointer;
}
details:first-of-type {
  border-radius: 10px 10px 0 0;
  border: 2px solid #000;
}
details:not(:first-of-type) {
  border: 2px solid #000;
  border-width: 0 2px 2px 2px;
}
details:last-of-type {
  border-radius: 0 0 10px 10px;
}
details summary {
  background-color: #808080;
  padding: 1em;
  color: #fff;
}
details .detail-content {
  border-width: 0 2px 0 2px;
  padding: 1.25em;
}

.mg-cards {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.mg-cards h2 {
  flex-basis: 100%;
}

#site-header {
  display: grid;
  grid-template-columns: minmax(0, calc((100vw - 1600px) / 2)) 1fr 1fr 1fr minmax(0, calc((100vw - 1600px) / 2));
  grid-template-areas: "space-l top top top space-r" "space-l brand ctas ctas space-r" "space-l brand menu menu menu";
}
#site-header .site-branding {
  grid-area: brand;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-around;
  max-width: 300px;
  text-align: center;
  background: lime;
}

#tag-line {
  margin-bottom: 0;
}

#top-nav {
  grid-area: top;
  background: #ed1c24;
}

#header-ctas {
  grid-area: ctas;
  background: lime;
}

#primary-nav {
  grid-area: menu;
  background: #64b7da;
}

#top-nav,
#header-ctas,
#primary-nav {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
#top-nav a,
#header-ctas a,
#primary-nav a {
  margin-left: 15px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  text-align: center;
}

section > h2:first-child {
  margin-top: 0;
  width: 100%;
}

section {
  padding: 30px 20% 44px;
  text-align: center;
}
section:nth-child(even) {
  background: #e0e0e0;
}

a.sg-no-style {
  all: revert;
}

#colors {
  text-align: center;
  padding: 30px 20%;
}
#colors h4 {
  margin: 0 0 10px 0;
  padding: 0;
}

.color-palette {
  display: flex;
  justify-content: space-evenly;
}

.color-swatch {
  display: flex;
  flex-direction: column;
  background: #fff;
  padding: 10px;
  border: 1px solid #000;
}

.swatch-name::before {
  content: "";
  height: 80px;
  width: 130px;
  display: block;
  background: lime;
  border: 1px solid #000;
}

.sec-swatch .swatch-name::before {
  background: #9d00ff;
}

.ter-swatch .swatch-name::before {
  background: #ffa834;
}

#buttons > p {
  max-width: 550px;
  margin: 0 auto 30px;
}
#buttons button,
#buttons .btn {
  margin: 10px 15px;
}

#typography {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
#typography h2 {
  width: 100%;
}
#typography .font-sampler {
  text-align: center;
}
#typography .font-sampler.first-font {
  font-family: "Source Sans Pro", sans-serif;
}
#typography .font-sampler.first-font h4 {
  font-family: "Source Sans Pro", sans-serif;
}
#typography .font-sampler.second-font {
  font-family: "PT Serif", serif;
}

#text-content {
  display: flex;
  flex-wrap: wrap;
  padding: 30px 10%;
  justify-content: center;
  gap: 30px;
}
#text-content h2 {
  width: 100%;
}
#text-content div {
  background: lightgreen;
}
#text-content #headings {
  width: 200px;
}
#text-content #common-text-elements {
  width: 800px;
  display: flex;
}
#text-content #common-text-elements div:first-child {
  width: 30%;
}
#text-content #common-text-elements div:last-child {
  width: 70%;
}
#text-content #misc-text-elements {
  width: 400px;
}
#text-content #in-out-elements {
  display: none;
}
#text-content #paragraphs {
  width: calc(35% - 15px);
  text-align: left;
}
#text-content #lists {
  display: flex;
  width: calc(65% - 15px);
  text-align: left;
  gap: 30px;
}

#media {
  padding: 30px 10%;
  display: flex;
  flex-wrap: wrap;
  gap: 2%;
}
#media div {
  width: 32%;
  background: lightcoral;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

#tables {
  display: flex;
  justify-content: center;
}

